import axios from 'axios'
const baseURL = '/players'

const getService = async () => {
    const res = await axios.get(baseURL)
    return res.data
}

const createService = async obj => {
    const res = await axios.post(baseURL,obj)
    return res.data
}

const deleteService = async id => {
    const res = axios.delete(`${baseURL}/${id}`)
    return res.data
}

const editService = async (id, obj) => {
    const res = await axios.put(`${baseURL}/${id}`, obj)
    return res.data
}

const toggleService = async (id, obj) => {
    const res = await axios.patch(`${baseURL}/${id}`, obj)
    return res.data
}

export default {getService, createService, deleteService, editService, toggleService}