import React, { useState, useEffect } from 'react'
import AddPlayer from './components/AddPlayer'
import DisplayPlayers from './components/DisplayPlayers'
import EditPlayer from './components/EditPlayer'
import Services from './services/players'

const App = () => {
  const [editing, setEditing] = useState(false)
  const [playerToEdit, setPlayerToEdit] = useState()
  const [players, setPlayers] = useState([])

  const addPlayer = async obj => {
    const data = await Services.createService(obj)
    setPlayers(players.concat(data))
  }

  const deletePlayer = async id => {
    const arr = [...players]
    await Services.deleteService(id)
    setPlayers(arr.filter(el => el.id !== id))
  }

  const whichPlayer = obj => {
    setEditing(true);
    setPlayerToEdit(obj);
  };

  const editPlayer = async (id, obj) => {
    const arr = [...players]
    const data = await Services.editService(id, obj)
    setPlayers(arr.map(x => x.id === id ? data : x))
    setEditing(false)
  };

  const toggle = async (id, obj) => {
    const arr = [...players]
    const data = await Services.toggleService(id, obj)
    setPlayers(arr.map(x => x.id === id ? data : x))
  };

  useEffect(() => {
    Services.getService().then(data => {
      setPlayers(data)
    })
  }, [])

  return (
    <div className="center">
      <h1>Practicing</h1>
      <hr />

      {editing ?
        <EditPlayer
          setEditing={setEditing}
          playerToEdit={playerToEdit}
          editPlayer={editPlayer}
        /> :
        <AddPlayer
          addPlayer={addPlayer}
        />}

      <DisplayPlayers
        players={players}
        deletePlayer={deletePlayer}
        whichPlayer={whichPlayer}
        toggle={toggle}
      />
    </div>
  )
}

export default App
